body {
  margin: 0;
  background-color: #0d0d0d;
  color: #00ff00;
  font-family: 'Courier New', Courier, monospace;
}

.container {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 1;
  position: relative;
}

.warning {
  text-align: center;
  font-size: 1.5em;
  padding: 2em;
  color: #ff3333;
}

.content-wrapper {
  display: flex;
  flex: 1;
  overflow: hidden;
}

.content-display {
  flex: 1;
  padding: 1em;
  border-right: 1px solid #333;
  white-space: pre-wrap;
  overflow-y: auto;
  position: relative;
  z-index: 2;
}

.folder-tree {
  width: 200px;
  padding: 1em;
  border-left: 1px solid #333;
  position: relative;
  z-index: 2;
}

.folder-tree p {
  margin: 0.5em 0;
  cursor: pointer;
}

.terminal {
  background-color: #000;
  padding: 1em;
  font-size: 0.9em;
  border-top: 1px solid #333;
  height: 120px;
  overflow-y: auto;
}

.globe-canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  pointer-events: none;
  opacity: 0.08;
}

.profile-card-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

@media (max-width: 768px) {
  .content-wrapper {
    flex-direction: column;
  }

  .folder-tree {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    border-left: none;
    border-bottom: 1px solid #333;
    padding: 0.5em;
    justify-content: center;
    background-color: #0d0d0d;
    gap: 1em;
  }

  .folder-tree p {
    margin: 0 1em;
    white-space: nowrap;
  }

  .content-display {
    flex: 1;
    padding: 1em;
    border-right: none;
  }
}